@font-face {
  font-family: "Bree Serif";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("Bree Serif Regular"), local("BreeSerif-Regular"),
    url(https://fonts.gstatic.com/s/breeserif/v8/4UaHrEJCrhhnVA3DgluA96rp5w.woff2)
      format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA,
    U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215,
    U+FEFF, U+FFFD;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Oxygen", "Ubuntu",
    "Fira Sans", "Droid Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#quote-box {
  background: whitesmoke;
  border-radius: 5px;
  font-family: "Bree Serif", sans-serif;
}

#text {
  color: #808080;
  font-size: 1.2rem;
  line-height: 1.5;
  -webkit-transition: color 3s;
  transition: color 3s;
  /* font-style: italic; */
}

.icon-quote-left {
  font-size: 1.35em;
}

#author {
  color: #a0a0a0;
  text-align: right;
  -webkit-transition: color 3s;
  transition: color 3s;
  /* font-style: italic; */
}

#new-quote:focus {
  outline: none;
}

#new-quote {
  border: none;
}

#tweet-quote:focus {
  outline: none;
}

@media screen and (min-width: 800px) {
  #quote-box {
    padding: 1.25rem;
  }

  .icon-quote-left {
    font-size: 1.5em;
    padding-right: 0.75rem;
  }

  #author {
    text-align: center;
    font-size: 1.15rem;
  }

  #text {
    font-size: 1.5rem;
    line-height: 1.5;
  }

  #new-quote,
  #tweet-quote {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 600px) {
  #quote-box {
    padding: 1.15rem;
  }

  #app-header {
    font-size: 1.5rem;
  }

  .icon-quote-left {
    font-size: 1.2em;
    padding-right: 0.75rem;
  }

  #author {
    text-align: center;
    font-size: 1.05rem;
  }

  #text {
    font-size: 1.25rem;
    /* line-height: 1.3; */
  }

  #new-quote,
  #tweet-quote {
    font-size: 1rem;
  }
}

@media screen and (max-width: 374px) {
  #quote-box {
    padding: 1rem;
  }

  #app-header {
    font-size: 1.25rem;
  }

  .icon-quote-left {
    font-size: 1.1em;
    padding-right: 0.5rem;
  }

  #author {
    text-align: center;
    font-size: 0.95rem;
  }

  #text {
    font-size: 1.1rem;
    /* line-height: 1.25; */
  }

  #new-quote,
  #tweet-quote {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 260px) {
  #quote-box {
    padding: 0.3rem;
    max-width: 100%;
    margin: 0;
  }

  .icon-quote-left {
    font-size: 1em;
    /* padding-right: 0.5rem; */
    display: block;
  }

  #author {
    text-align: center;
    font-size: 0.75rem;
  }

  #text {
    font-size: 0.85rem;
    /* line-height: 1.25; */
  }

  #new-quote,
  #tweet-quote {
    font-size: 0.75rem;
    margin-top: 0.2rem;
  }
}

@font-face {
  font-family: "icomoon";
  src: url(/random-quote-machine/static/media/icomoon.ab3e32da.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-quote-left:before {
  content: "\F10D";
}

